function DeleteButton(props) {
    return (
        <button
            disabled={props.disabled}
            className='icon delete'
            onClick={props.onClickHandler}
        />
    );
}


function StarButton(props) {
    return (
        <button
            disabled={props.disabled}
            className={'icon ' + (props.starred? 'starred' : 'unstarred')}
            onClick={props.onClickHandler}
        />
    );
}


function ActionsBlock(props) {
    return ([
        <td key={0}>
            <DeleteButton
                disabled={props.disabled}
                onClickHandler={props.deleteButtonHandler}
            />
        </td>,
        <td key={1}>
            <StarButton
                disabled={props.disabled}
                onClickHandler={props.starButtonHandler}
                starred={props.starred}
            />
        </td>
    ]);
}


class LoadingFact extends React.Component {

    maxNPoints = 4;
    timeBetweenPoints = 200; // milliseconds

    constructor(props) {
        super(props);
        this.state = {loadingNPoints: 0};
    }

    componentDidMount() {
        this.interval = setInterval(() => this.tick(), this.timeBetweenPoints);
    }

    tick() {
        this.setState({
            loadingNPoints: (this.state.loadingNPoints + 1) % this.maxNPoints,
        });
    }

    componentWillUnmount() {
        if (this.interval) {
            clearInterval(this.interval);
        }
    }

    render() {
        return 'Loading ' + '.'.repeat(this.state.loadingNPoints);
    }

}


function TextBlock(props) {
    let text;
    if (props.fact !== null) {
        if (props.starred) {
            text = <strong>{props.fact}</strong>;
        } else {
            text = props.fact;
        }
    } else {
        text = <LoadingFact />;
    }
    return (
        <td className="textCell">
            {text}
        </td>
    )
}


class ChuckFactContainer extends React.Component {
    constructor(props) {
        super(props);
        const starred = (('FACT-ID:' + this.props.factId) in localStorage);
        this.state = {starred: starred};
    }

    componentDidMount() {
        window.scroll({top: document.body.scrollHeight, behavior: 'smooth'});
    }

    starButtonHandler = () => {
        const starred = !this.state.starred;
        this.setState({starred: starred});
        if (starred){
            localStorage.setItem('FACT-ID:' + this.props.factId, this.props.fact);
        } else {
            localStorage.removeItem('FACT-ID:' + this.props.factId);
        }
    }

    deleteButtonHandler = () => {
        if (this.state.starred) {
            localStorage.removeItem('FACT-ID:' + this.props.factId);
        }
        this.props.deleteButtonHandler(this.props.factId);
    }

    render() {
        return (
            <tr>
                <ActionsBlock
                    disabled = {this.props.fact === null}
                    starred = {this.state.starred}
                    deleteButtonHandler={this.deleteButtonHandler}
                    starButtonHandler={this.starButtonHandler}
                />
                <TextBlock
                    starred = {this.state.starred}
                    fact = {this.props.fact}
                />
            </tr>
        );
    }

}


function NewFactButton(props) {
    const text = (props.zeroFacts ? 'Add a Chuck Norris fact' : 'Add another Chuck Norris fact');
    return (
        <button
            id="add-fact-button"
            className="block"
            onClick={props.addNewFact}
        >
            {text}
        </button>
    );
}


export default class ChuckFactsManager extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            facts: [
                // ['043', 'lalala1'],
                // ['0434', 'lalala2'],
                // ['r043', 'lalala3'],
            ],
            // errorMsgs: [],
        };
        this.nFactsRequested = 0;  // I use the TOTAL number of requested facts as a key for currently loading facts
        this.deletedFacts = [];
    }

    componentDidMount() {
        const savedFacts = [];
        for (const [key, value] of Object.entries(localStorage)) {
            const splittedKey = key.split('FACT-ID:');
            if (splittedKey.length > 1) {
                console.log(key, value);
                savedFacts.push([splittedKey[1], value]);
            }
        }
        if (savedFacts.length > 0) {
            this.setState({facts: savedFacts});
        }
    }

    errorWhileGettingFact(loadingFactId, errorMsg) {
        console.log('errorWhileGettingFact: ' + errorMsg);
        this.deleteFact(loadingFactId);
        // Popup.alert(errorMsg, 'An error has occurred :(');
        // this.setState({errorMsgs: this.state.errorMsgs.concat(errorMsg)});
        const errorMsgRendered =
            <div className="popup">
                <strong>An error has occurred :(</strong><br/>
                {errorMsg}
            </div>;
        toaster.notify(errorMsgRendered, {
            duration: 3000,
            position: "bottom",
        });
    }

    // isKnownFact(factId) {
    //     for (const [id, dummy] of this.state.facts) {
    //         if (id === factId) {
    //             return true;
    //         }
    //     }
    //     return false;
    // }

    getFactIdx(facts, factId) {
        for (const i in facts) {
            if (facts.hasOwnProperty(i) && facts[i][0] === factId) {
                return i;
            }
        }
        return -1;
    }

    restoreFact(fact, idx) {
        this.setState((state) => {
            const newFacts = state.facts.slice();
            newFacts.splice(idx, 0, fact);
            return {facts: newFacts};
        });
    }

    deleteFact(factId) {
        this.setState((state) => {
            const newFacts = state.facts.slice();
            const idx = this.getFactIdx(newFacts, factId);
            if (idx >= 0){
                const factToDelete = state.facts[idx];
                if (factToDelete[1] !== null) {
                    // this.deletedFacts.push([[idx, ]]);
                    // toaster.notify('Undo delete', {
                    //     duration: 5000,
                    //     position: "bottom",
                    // });
                    toaster.notify(({onClose}) =>
                            (
                                <button className="toast-button"
                                   onClick={
                                       () =>
                                           {
                                               this.restoreFact(factToDelete, idx);
                                               onClose();
                                           }
                                       }
                                >
                                    <img id="undo" src={undo_arrow} alt="Undo"/>
                                    Undo delete
                                </button>
                            ),
                        {
                            duration: 5000,
                            position: "bottom",
                        });

                    // toaster.notify(
                    //             <a className="toast" href="#"
                    //                onClick={
                    //                    () =>
                    //                    {
                    //                        this.restoreFact(factToDelete, idx);
                    //                    }
                    //                }
                    //             >
                    //                 Undo delete
                    //             </a>,
                    //     {
                    //         duration: 2000,
                    //         position: "bottom",
                    //     });


                    // toaster.notify(({ onClose }) => (
                    //     <a href="#" css={{ textDecoration: "none" }} onClick={onClose}>
                    //         {/*<Layer*/}
                    //         {/*    elevation="md"*/}
                    //         {/*    css={{*/}
                    //         {/*        overflow: "hidden",*/}
                    //         {/*        alignItems: "center",*/}
                    //         {/*        display: "flex"*/}
                    //         {/*    }}*/}
                    //         {/*>*/}
                    //             <img src="https://randomuser.me/api/portraits/men/32.jpg" />
                    //             <div css={{ padding: "1rem" }}>
                    //                Did you know?
                    //
                    //                     You can customize the notification using a render
                    //                     callback.
                    //
                    //             </div>
                    //         {/*</Layer>*/}
                    //     </a>
                    // ));
                }
                newFacts.splice(idx, 1);
                return {facts: newFacts};
            }
        });
    }

    getChuckFact(config, loadingFactId, nAttempts=1) {
        axios.get('https://api.chucknorris.io/jokes/random', config)
            .then(res => {
                console.log(res.data.categories);
                if (this.getFactIdx(this.state.facts, res.data.id) === -1) {
                    this.setState((state) => {
                        const newFacts = state.facts.slice();
                        const idx = this.getFactIdx(newFacts, loadingFactId);
                        newFacts[idx] = [res.data.id, res.data.value];
                        return {facts: newFacts};
                    });
                } else {
                    console.log(`Got an already known fact. Attempted ${nAttempts} times.`);
                    if (nAttempts >= 2) { //fixme
                        this.errorWhileGettingFact(
                            loadingFactId,
                            'Max number of attempts reached. ' +
                            'It seems that you already know all the facts!');
                    } else {
                        this.getChuckFact(config, loadingFactId,nAttempts + 1);
                    }
                }
            })
            .catch(error => {
                console.log(error);
                this.errorWhileGettingFact(loadingFactId, error.toString());
            });
    }

    addNewFact(loadingFactId) {
        let config = undefined;
        if (!this.props.showNsfwFacts) {
            config = {
                params: {
                    // category: 'animal,career,celebrity,dev,fashion,food,history,money,movie,music,' +
                    //     'political,religion,science,sport,travel',
                    category: 'animal'
                }
            }
        }
        this.getChuckFact(config, loadingFactId);
    }

    newFactButtonHandler = () => {
        const loadingFactId = 'loading' + String(this.nFactsRequested);
        this.nFactsRequested++;
        this.setState((state) => ({
                facts: state.facts.concat([[loadingFactId, null]])
            })
        );
        this.addNewFact(loadingFactId);
    }

    deleteButtonHandler = (factId) => {
        this.deleteFact(factId);
    }

    render() {
        const chuckFactsRendered = this.state.facts.map(
            ([id, fact]) =>
                <ChuckFactContainer
                    key={id}
                    factId={id}
                    fact={fact}
                    deleteButtonHandler={this.deleteButtonHandler}
                />
        );

        return (
            <div id="chuck-fact-manager">
                <div id="chuck-fact-manager-main-region">
                    <table>
                        <tbody>
                            {chuckFactsRendered}
                        </tbody>
                    </table>
                </div>
                <div id="new-fact-region">
                    <NewFactButton
                        zeroFacts={this.state.facts.length === 0}
                        addNewFact={this.newFactButtonHandler}
                    />
                </div>
                {/*{errorPopups}*/}
            </div>
        );
    }

}
